/* 칵테일 시즌 메뉴 타이틀 */
.cocktail-title {
    margin-top: 32px;

    font-family: "Yoon YMjO 550_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 138% */

    text-align: center;
    letter-spacing: -0.25px;

    /* white */

    color: #ffffff;
}

.cocktail-price {
    margin-top: 8px;

    font-family: "Yoon YMjO 540_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    /* identical to box height, or 117% */

    text-align: center;
    letter-spacing: 0.25px;

    /* white */

    color: #ffffff;
}

/* 칵테일 시즌 메뉴 본문 */
.cocktail-season-content {
    margin-top: 24px;
}

.cocktail-season-content-detail-l {
    display: flex;
}

.cocktail-season-content-detail-l-img {
    margin: 0 16px 0 32px;
    /* display: flex; */
    width: 60px;
    height: 80px;
}

.cocktail-season-content-detail-r {
    margin-top: 24px;
    display: flex;
    justify-content: right;
}

.cocktail-season-content-detail-r-img {
    margin: 0 32px 0 16px;
    /* display: flex; */
    width: 60px;
    height: 80px;
}

.cocktail-season-content-detail-title {
    width: 170px;

    font-family: "Yoon YMjO 550_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 138% */

    letter-spacing: -0.25px;

    /* white */

    color: #ffffff;
}

.cocktail-season-content-detail-category {
    width: 170px;
    margin-top: 12px;

    font-family: "Yoon YMjO 540_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 14px;
    /* identical to box height, or 175% */

    letter-spacing: 0.25px;

    /* gray2 */

    color: #666666;
}

.cocktail-season-content-detail-desc {
    margin-top: 4px;
    font-family: "Yoon YMjO 520_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    /* identical to box height, or 117% */

    letter-spacing: 0.25px;

    /* white */

    color: #ffffff;
}

