.wine-category {
    font-family: "Yoon YMjO 550_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    /* identical to box height, or 138% */

    margin-top: 32px;
    text-align: center;
    letter-spacing: -0.25px;

    /* white */

    color: #ffffff;
}

.wine-align {
    display: flex;
    justify-content: center;
    margin-top: 24px;
}

.wine-title {
    width: 286px;
    font-family: "Yoon YMjO 540_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    /* identical to box height, or 117% */

    margin-right: 16px;
    letter-spacing: 0.25px;

    /* white */

    color: #ffffff;
}

.wine-price {
    width: 24px;
    font-family: "Yoon YMjO 540_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    /* identical to box height, or 117% */

    text-align: right;
    letter-spacing: 0.25px;

    /* white */

    color: #ffffff;
}

.devide-marker {
    font-family: "Yoon YMjO 540_TT";
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 14px;
    /* identical to box height, or 175% */

    margin-top: 32px;
    text-align: center;
    letter-spacing: 0.25px;

    /* white */

    color: #ffffff;
}
